
    <template>
      <section class="market-doc-container">
        <h2>Input 输入框</h2>
<h3>基本用法</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    type=&quot;text&quot;
    placeholder=&quot;请输入用户名&quot;
    v-model=&quot;value&quot;
  /&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        value: ''
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>大小尺寸</h3>
<p>可通过 size 属性指定输入框的尺寸，除了默认的大小外，还提供了 large、small 和 mini 三种尺寸</p>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-input
      class=&quot;doc-mb10&quot;
      type=&quot;text&quot;
      size=&quot;large&quot;
      placeholder=&quot;请输入用户名&quot;
      v-model=&quot;value1&quot;
    /&gt;
    &lt;mku-input
      class=&quot;doc-mb10&quot;
      type=&quot;text&quot;
      size=&quot;default&quot;
      placeholder=&quot;请输入用户名&quot;
      v-model=&quot;value2&quot;
    /&gt;
    &lt;mku-input
      type=&quot;text&quot;
      size=&quot;small&quot;
      placeholder=&quot;请输入用户名&quot;
      v-model=&quot;value3&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    name: 'ExampleInput',
    data () {
      return {
        value1: '',
        value2: '',
        value3: ''
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>多行文本框</h3>
<p>支持高度随内容自动变化</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    type=&quot;textarea&quot;
    placeholder=&quot;多行文本框&quot;
    v-model=&quot;value&quot;
    autosize
  /&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    name: 'ExampleInput',
    data () {
      return {
        value: ''
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>禁用状态</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    type=&quot;text&quot;
    placeholder=&quot;禁用状态&quot;
    v-model=&quot;value5&quot;
    :disabled=&quot;true&quot;
  /&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  name: 'ExampleInput',
  data () {
    return {
      value5: ''
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>只读</h3>
<mku-example>
        
        <template slot="source"><element-demo4 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    type=&quot;text&quot;
    placeholder=&quot;只读的输入框&quot;
    readonly
    v-model=&quot;value6&quot;
  /&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  name: 'ExampleInput',
  data () {
    return {
      value6: ''
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>可清除</h3>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    type=&quot;text&quot;
    placeholder=&quot;可清除&quot;
    v-model=&quot;value7&quot;
    clearable
    @clear=&quot;handleClear&quot;
  /&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  name: 'ExampleInput',
  data () {
    return {
      value7: ''
    }
  },
  methods: {
    handleClear () {
      alert('handleClear')
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>焦点</h3>
<mku-example>
        
        <template slot="source"><element-demo6 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-input
      ref=&quot;input&quot;
      type=&quot;text&quot;
      placeholder=&quot;请输入用户名&quot;
      v-model=&quot;value2&quot;
    /&gt;
    &lt;mku-button type=&quot;primary&quot; @click=&quot;handleButtonClick&quot;&gt;
      使得input获取焦点
    &lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  name: 'ExampleInput',
  data () {
    return {
      value: '',
      value2: ''
    }
  },
  methods: {
    handleButtonClick () {
      this.$refs.input.focus()
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>自动铺满</h3>
<mku-example>
        
        <template slot="source"><element-demo7 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-input
    class=&quot;doc-mb10&quot;
    placeholder=&quot;块级属性将自动铺满父级容器&quot;
    block
  /&gt;
  &lt;mku-input
    type=&quot;textarea&quot;
    placeholder=&quot;块级属性将自动铺满父级容器&quot;
    block
  /&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>Input Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
<th style="text-align:center">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">输入框类型</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>text</code>、<code>password</code>、<code>textarea</code>等所有原生输入框支持的类型</td>
<td style="text-align:center"><code>text</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">value、v-model</td>
<td style="text-align:left">绑定的值，v-model可以进行双向数据绑定</td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">input尺寸，仅在非<code>textarea</code>下有效</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>large</code>、<code>default</code>、<code>small</code></td>
<td style="text-align:center"><code>default</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">readonly</td>
<td style="text-align:left">是否只读</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">输入框类型</td>
<td style="text-align:center"><code>Strimg</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">clearable</td>
<td style="text-align:left">是否可以清除输入框，仅在非<code>textarea</code>下有效</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">autosize</td>
<td style="text-align:left">textarea高度虽内容自动变化</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">rows</td>
<td style="text-align:left">textarea默认的行数</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">autofocus</td>
<td style="text-align:left">自动获取焦点</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">block</td>
<td style="text-align:left">块级属性，文本框将自动铺满父级容器</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
<h3>Input Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">focus</td>
<td style="text-align:left">组件获取焦点时触发</td>
<td style="text-align:left">Event</td>
</tr>
<tr>
<td style="text-align:left">blur</td>
<td style="text-align:left">组件失去焦点时触发</td>
<td style="text-align:left">Event</td>
</tr>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">组件值发生变化时触发</td>
<td style="text-align:left">value</td>
</tr>
<tr>
<td style="text-align:left">clear</td>
<td style="text-align:left">组件由clearable属性定义的按钮清空时触发</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">clear</td>
<td style="text-align:left">按下回车时触发</td>
<td style="text-align:left">event，当前值</td>
</tr>
</tbody>
</table>
<h3>Input Methods</h3>
<table>
<thead>
<tr>
<th style="text-align:left">方法名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">focus</td>
<td style="text-align:left">使组件获取焦点</td>
<td style="text-align:left">Event</td>
</tr>
<tr>
<td style="text-align:left">blur</td>
<td style="text-align:left">使组件失去焦点</td>
<td style="text-align:left">Event</td>
</tr>
</tbody>
</table>

